import back from '@/components/back'
import noData from '@/components/noData'

export default {
    components:{
        back,
        noData
    },
    data(){
        return {
            search: '',
            page: 1,
            haveMore: true,
            list: [],
            orderInfo: this.$store.state.pInfo,
            user: this.$store.state.userInfo,
            pageShow: false
        }
    },
    created(){
        this.pull(false)
    },
    mounted(){
        this.getMore()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods:{
        pull(h){
            if(this.haveMore){
                this.$post('PlatFormAPI/KnowledgeBase/GetDrQuestionnaireForPage',this.$pick({
                    DrId: this.user.Data.User.DoctorId,
					QuestionnaireName:this.search,
					OrgIds: this.user.Data.User.OrgId,
					QuestionnaireKey:'',
                    PageIndex: this.page,
                    PageSize: 15,
                })).then(o => {
                    this.pageShow = true
                    if (!o.ReturnData || o.ReturnData.length < 15){
                        this.haveMore = false
                    }
                    else {
                        this.haveMore = true
                    }
                    
                    if (h){
                        o.ReturnData.forEach(v => {
                            this.list.push(v)
                        })
                    }
                    else {
                        this.list = o.ReturnData
                    }
                })
            }
        },
        check(i){
			this.$bridge.callhandler('createWebView',i.QuestionnaireUrl)
            //this.$router.push(`/follow-up?id=${i.PlanTempId}&fromList=1`)
        },
        getMore(){
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight

                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.pull(true)
                    }
                }, 100)
            }
        },
        send(i){
            this.$store.dispatch('pushserviceQuestionnaire',i)
            this.$router.back()
        },
        searchFor(){
            this.haveMore = true
            this.page = 1
            this.pull(false)
        }
    }
}



